<script setup>
import { ref, reactive, onMounted, watch } from 'vue'
import { getIconPath, getImagePath } from '@/utils/paths.js'
import { useI18n } from 'vue-i18n'
import ItemRouterLink from '@/components/Item/ItemRouterLink.vue'

const { t } = useI18n()

const props = defineProps({
  item: Object
})

const emit = defineEmits(['open-material-modal'])

// Local state per character
const characterState = reactive({
  level: 1,
  strangeling: false,
  makeovers: [],
  affinity: 0,
  powerUp: 0
})

const STORAGE_KEY = `character_${props.item.id}`

// Load saved state from localStorage
onMounted(() => {
  const saved = localStorage.getItem(STORAGE_KEY)
  if (saved) {
    Object.assign(characterState, JSON.parse(saved))
  }
})

// Save to localStorage whenever characterState changes
watch(characterState, (newVal) => {
  localStorage.setItem(STORAGE_KEY, JSON.stringify(newVal))
}, { deep: true })

// Toggle functions
function toggleStrangeling() {
  characterState.strangeling = !characterState.strangeling
}

function toggleMakeover(number) {
  const index = characterState.makeovers.indexOf(number)
  if (index > -1) characterState.makeovers.splice(index, 1)
  else characterState.makeovers.push(number)
}

function selectAffinity(number) {
  characterState.affinity = number
}

function selectPowerUp(number) {
  characterState.powerUp = number
}

const aaaa = [
