<script setup>
import { ref } from 'vue'
import { DATA } from '@/utils/constants.js';
import { useI18n } from 'vue-i18n'
import { getImagePath } from '@/utils/paths.js'

const { t } = useI18n()
const items = DATA.GIFTS.data || [];
const props = defineProps({ item: Object });

const copyButtonText = ref('Copy Code')

function copyCode() {
  if (!props.item?.code) return
  navigator.clipboard.writeText(props.item.code).then(() => {
    copyButtonText.value = 'Copied!'
    setTimeout(() => copyButtonText.value = 'Copy Code', 1500)
  })
}

function formatDate(timestamp) {
  const date = new Date(timestamp);
  return date.toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });
}

</script>

<template>
    <div class="card-body" data-category="gift-code">
    <div class="gift-code">{{ item.code }}</div>
    <div class="flex justify-end my-2">
      <button @click="copyCode">{{ copyButtonText }}</button>
    </div> 

    <time v-if="item.timestamp" :datetime="item.timestamp">{{ formatDate(item.timestamp) }}</time>

    <div class="panel">
      <table class="data-table">
        <caption>{{ item.label }}</caption>
        <thead>
          <tr>
            <th>Item</th>
            <th class="col-quantity">Quantity</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="reward in item.rewards" :key="reward.id">
            <td>
              <img :src="getImagePath(reward.id)" class="icon-sm" alt />
              {{ t(reward.id) }}
            </td>
            <td class="col-quantity">{{ reward.quantity }}</td>
          </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>
