<script setup>
import { useI18n } from 'vue-i18n'
import { getImagePath, getIconPath } from '@/utils/paths.js'
import ShopList from '@/components/Item/ShopList.vue'
import MonsterList from '@/components/Item/MonsterList.vue'
import HowToObtainRecipe from '@/components/Item/HowToObtainRecipe.vue'
import { DATA } from '@/utils/constants.js'
import { ref, computed, onMounted, onBeforeUnmount, inject } from 'vue'
import IconElement from '@/components/Item/IconElement.vue'
import IconRarity from '@/components/Item/IconRarity.vue'
import ItemRouterLink from '@/components/Item/ItemRouterLink.vue'
const { t } = useI18n()
const props = defineProps({ itemId: String })

function getItemFromId(itemId) {
  if (!itemId) return null
  const parts = itemId.split('.')
  if (parts.length < 2) return null

  const key = parts[1].toUpperCase() // e.g., "WEAPONS"
  // console.log(DATA[key])
  const list = DATA[key]?.data
  if (!list) return null

  return list.find(item => item.id === itemId) || null
}

const item = computed(() => getItemFromId(props.itemId))
</script>

<template>
  <div v-if="item">

    <IconRarity :rarity="item.rarity" />
    <ItemRouterLink :item="item" />
    <HowToObtainRecipe v-if="item.crafter" :item="item" />
    <div class="panel">
      <img :src="getIconPath('/img/items/NPCs/Merchant.png')" alt="Exp" class="icon-md" />
      <ShopList :itemId="item.id" />
    </div>
    <div class="panel">
      <img :src="getIconPath('/img/icons/others/monsters.png')" alt="Exp" class="icon-md" />
      <MonsterList :itemId="item.id" />
    </div>

    <div v-for="(recipe, index) in item.recipes" :key="index">
      <div class="panel">
        <img :src="getIconPath('/img/icons/others/recipe.png')" alt="Exp" class="icon-md" />
        <table class="data-table">
          <thead>
            <tr>
              <th scope="col">
                Materials
              </th>
              <th class="col-quantity" scope="col">
                Required
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(material, index) in recipe.materials" :key="index">
              <td>
                <img :src="getImagePath(material.id)" class="icon-sm" alt>
                {{ t(material.id) }}
              </td>
              <td class="col-quantity"><strong>{{ material.quantity }}</strong></td>
            </tr>
          </tbody>
        </table>
        <div class="recipe-crafting-stat">
          <span class="text">Recommended</span>
          <img :src="getIconPath(`/img/icons/stats/stat-life-${item.crafter}.png`)" class="icon-sm" :alt="item.crafter">
          <span class="stat-value">
            {{ recipe.crafting_stat }}
          </span>
        </div>
        <div class="experience-gain">
          <img :src="getIconPath(`/img/icons/stats/stat-exp.png`)" alt="Exp" class="icon-sm" />
          {{ recipe.exp?.toLocaleString() }}
        </div>
      </div>
      <div class="panel">
        <img :src="getIconPath('/img/items/NPCs/Merchant.png')" alt="Exp" class="icon-md" />
        
        <table class="data-table">
          <thead>
            <tr>
              <th>Materials</th>
              <th>Shops</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(material, index) in recipe.materials" :key="index">
              <td>
                <img :src="getImagePath(material.id)" class="icon-sm" alt>
                {{ t(material.id) }}
              </td>
              <td>
                <ShopList :itemId="material.id" />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="panel">
        <img :src="getIconPath('/img/icons/others/monsters.png')" alt="Exp" class="icon-md" />
        <table class="data-table">
          <thead>
            <tr>
              <th>Materials</th>
              <th>Enemies</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(material, index) in recipe.materials" :key="index">
              <td>
                <img :src="getImagePath(material.id)" class="icon-sm" alt>
                {{ t(material.id) }}
              </td>
              <td>
                <MonsterList :itemId="material.id" />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

  </div>

</template>
