import { createRouter, createWebHistory } from 'vue-router';
import Home from '@/pages/Home.vue';
import Translations from '@/pages/Translations.vue';
import ItemList from '@/pages/ItemList.vue';
import RecipeSourceList from '@/pages/recipeSourceList.vue';
import ItemProfile from '@/pages/ItemProfile.vue';

const profileProps = (route) => ({
  tiers: [route.params.menuTier1],
  menuTier1: route.params.menuTier1,
  id: route.params.id,
});

const listProps = (route) => ({
  tiers: [
    route.params.menuTier1,
    route.params.menuTier2,
    route.params.menuTier3,
  ],
  menuTier1: route.params.menuTier1,
  menuTier2: route.params.menuTier2,
  menuTier3: route.params.menuTier3,
});

const routes = [
  {
    path: '/list/:menuTier1/:menuTier2?/:menuTier3?',
    name: 'ItemList',
    component: ItemList,
    props: listProps,
  },
  {
    path: '/recipes/:menuTier1/:menuTier2?/:menuTier3?',
    name: 'RecipeList',
    component: ItemList,
    props: listProps,
  },
  {
    path: '/items/:menuTier1/:id',
    name: 'ItemProfile',
    component: ItemProfile,
    props: profileProps,
  },
  {
    path: '/others/:menuTier1/:id',
    name: 'otherProfile',
    component: ItemProfile,
    props: profileProps,
  },
  {
    path: '/gifts/:menuTier1/:id',
    name: 'codeProfile',
    component: ItemProfile,
    props: profileProps,
  },
  {
    path: '/recipe-source',
    name: 'recipeSourceList',
    component: RecipeSourceList
  },
  { path: '/', name: 'Home', component: Home },
  { path: '/recipes', redirect: '/recipes/weapons' },
  { path: '/translations', name: 'Translations', component: Translations },
];

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) return savedPosition;
    return { top: 0 };
  },
});

export default router;
