export const sortBy = {
  name: (a, b, dir, translationMap) => {
    const valA = translationMap?.get(a.id) || a.name || ''
    const valB = translationMap?.get(b.id) || b.name || ''
    return dir === 'asc'
      ? valA.localeCompare(valB, undefined, { numeric: true, sensitivity: 'base' })
      : valB.localeCompare(valA, undefined, { numeric: true, sensitivity: 'base' })
  },
  rarity: (a, b, dir) => {
    const diff = (a.rarity ?? 0) - (b.rarity ?? 0)
    return dir === 'asc' ? diff : -diff
  }
}
