<script setup>
import { DATA } from '@/utils/constants.js';
import { useI18n } from 'vue-i18n';
const { t } = useI18n();
import { getIconPath } from '@/utils/paths.js';

function isRecipe(type) {
  return type?.includes(' Recipe');
}

function getShopIdForItem(itemId) {
  const shop = DATA.SHOPS.data.find(shop =>
    shop.stocks.some(
      stock => stock.id === itemId && !isRecipe(stock.type)
    )
  );
  return shop ? shop.id : null;
}

const props = defineProps({
  itemId: {
    type: String,
    required: true
  }
})
</script>

<template>
  <div v-if="getShopIdForItem(itemId)">
    <img :src="getIconPath(`/img/icons/others/materials.png`)" class="icon-sm" alt>
    {{ t(getShopIdForItem(itemId)) }}
  </div>
</template>

