<script setup>
import { ref, computed, onMounted, watch } from 'vue'
import { getImagePath, getIconPath, itemIdToRoute } from '@/utils/paths.js'
import { useI18n } from 'vue-i18n'
import { DATA } from '@/utils/constants.js'
import TrackerCheckbox from '@/components/Shared/TrackerCheckbox.vue'

const { t } = useI18n()
const props = defineProps({ item: Object })
const emit = defineEmits(['open-material-modal'])

const iconMap = {
  weapons: '/img/icons/life/weapon.png',
  armors: '/img/icons/outfits/armors.png',
  shields: '/img/icons/life/equipment-shield.png',
  tools: '/img/icons/life/equipment-blacksmith.png',
  consumables: '/img/icons/others/consumables.png',
  objects: '/img/icons/others/objects.png',
  materials: '/img/icons/others/materials.png',
  recipes: '/img/icons/others/recipe.png',
  other: '/img/icons/others/valuables.png',
  valuables: '/img/icons/others/valuables.png',
}

const activeTab = ref('')

const categories = computed(() => {
  return props.item.list.map(entry => entry.category)
})

watch(categories, () => {
  if (!activeTab.value && categories.value.length) activeTab.value = categories.value[0]
})

const filteredEntries = computed(() => {
  return props.item.list.filter(entry => entry.category === activeTab.value)
})

function getUsedCurrency(item) { return item.currency || 'dosh' }

function findPrince(stockId, item) {
  try {
    const [, keyPart] = stockId.split('.')
    const KEY = (keyPart || 'OBJECTS').toUpperCase()
    const foundItem = DATA[KEY]?.data?.find(i => i.id === stockId)
    const currency = getUsedCurrency(item)
    const currencyIcon = {
      dosh: '/img/icons/others/dosh.png',
      celestias_gift: '/img/icons/others/celestias-gift.png',
      cashnuts: '/img/icons/others/cashnut.png',
      goddess_fruit: '/img/icons/others/goddess-fruit.png'
    }
    return {
      price: foundItem?.price?.buy?.[currency] ?? null,
      iconPath: currencyIcon[currency] ?? '/img/image-not-found.png',
      size: foundItem?.size ?? null
    }
  } catch { return { price: null, iconPath: '/img/image-not-found.png', size: null } }
}
</script>

<template>
  <div class="card-body" data-category="shop">

    <div class="shop-location">
      {{ item.location }}
    </div>

    <RouterLink :to="itemIdToRoute(item.id)" class="item-route-link">
      <img v-if="item.icon" :src="getIconPath(item.icon)" class="icon-lg" alt>
      <img v-else :src="getIconPath('/img/icons/others/bag.png')" class="icon-lg" alt>
      <h2>{{ item.label }}</h2>
    </RouterLink>

    <!-- Tabs List -->
    <ul class="tab-list">
      <li v-for="category in categories" :key="category" class="tab-item">
        <a href="#"
          @click.prevent="activeTab = category"
          :class="activeTab === category ? 'tab is-active' : 'tab'">
          <img :src="getIconPath(iconMap[category])" class="icon-md" alt>
          <span class="link-label">{{ category }}</span>
        </a>
      </li>
    </ul>

    <!-- Tab Content -->
    <!-- <div>
      <button>Check all recipe</button>
    </div> -->

    <div class="scrollable">

      <ul v-for="entry in filteredEntries" :key="entry.category" class="inventory-list">
        <li v-for="stock in entry.stocks" :key="stock.id"
          :data-restriction="stock.restriction ? 'true' : ''"
          class="inventory-item">

          <div class="row">

            <div class="column">
              <img :src="getImagePath(stock.id)" class="icon-sm item-image" alt />
              {{ t(stock.id) }}
              <span v-if="entry.size">{{ entry.size }}</span>
            </div>

            <div class="column">

              <div class="item-stock" v-if="stock.limit">
                <img :src="getIconPath('/img/icons/others/crate.png')" class="h-[22px]" alt>
                <span class="stock-count">{{ stock.limit }}</span>
              </div>

              <div class="item-size" v-if="entry.category === 'objects'">
                <span class="material-symbols-outlined">grid_on</span>
                {{ findPrince(stock.id, item).size }}
              </div>

              <div class="item-price">
                <img :src="getIconPath(findPrince(stock.id, item).iconPath)" class="h-[18px]" alt>

                <span v-if="stock.price">{{ stock.price }}</span>
                <span v-else>{{ findPrince(stock.id, item).price }}</span>

              </div>
              
              <TrackerCheckbox v-if="entry.category === 'recipes'" :item="stock" />
             
            </div>
          </div>

          <ul class="inventory-list" v-if="stock.recipes">
            <li v-for="recipe in stock.recipes" class="inventory-item">
              <div class="row">
                <div class="column">
                  <img :src="getImagePath(recipe.id)" class="icon-sm item-image" alt />
                  {{ t(recipe.id) }}
                </div>
                <div class="column">
                  <TrackerCheckbox :showIcon="true" :item="recipe" />
                </div>
              </div>
            </li>
          </ul>

        </li>
      </ul>
    </div>

  </div>
</template>
