import { ref, computed, watch } from 'vue'

const STORAGE_KEY = 'tracker_checked_items'

const checkedItems = ref([])

export const manageTracker = (() => {
  const load = () => {
    const saved = localStorage.getItem(STORAGE_KEY)
    try {
      const arr = saved ? JSON.parse(saved) : []
      return Array.isArray(arr) ? arr : []
    } catch {
      return []
    }
  }

  const save = (data) => {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(data))
  }

  const clear = () => {
    localStorage.removeItem(STORAGE_KEY)
    checkedItems.value = []
  }

  const hasSaveData = () => !!localStorage.getItem(STORAGE_KEY)

  return { load, save, clear, hasSaveData }
})()

// Initialize
if (manageTracker.hasSaveData()) {
  checkedItems.value = manageTracker.load()
}

// Save on change
checkedItems.value = checkedItems.value || []
checkedItems.value && checkedItems.value.length
watch(
  checkedItems,
  (newValue) => manageTracker.save(newValue),
  { deep: true }
)

// Helpers for checkboxes
export const trackerStore = {
  checkedItems,
  isChecked: (id) => checkedItems.value.includes(id),
  toggle: (id) => {
    const idx = checkedItems.value.indexOf(id)
    if (idx >= 0) checkedItems.value.splice(idx, 1)
    else checkedItems.value.push(id)
  }
}
