<script setup>
import { getImagePath, getIconPath, itemIdToRoute } from '@/utils/paths.js'
import ShopList from '@/components/Item/ShopList.vue'
import LocationList from '@/components/Item/LocationList.vue'
import StatsTable from '@/components/Item/StatsTable.vue'
import IconElement from '@/components/Item/IconElement.vue'
import IconRarity from '@/components/Item/IconRarity.vue'
import IconRank from '@/components/Item/IconRank.vue'
import { useI18n } from 'vue-i18n'
import { DATA } from '@/utils/constants.js'
import ItemRouterLink from '@/components/Item/ItemRouterLink.vue'
import RecipeTable from '@/components/Item/RecipeTable.vue'

const { t } = useI18n()
const props = defineProps({
  item: Object,
})

const emit = defineEmits(['open-material-modal'])

function findPrince(itemId) {
  const KEY = itemId.split('.')[1].toUpperCase();
  const foundItem = DATA[KEY].data.find(item => item.id === itemId);
  return foundItem.price?.buy;
}

const iconMap = {
  pickaxes: 'miner',
  hoes: 'farmer',
  'frying-pans': 'cook',
  hammers: 'blacksmith',
  needles: 'tailor',
  axes: 'Woodcutter',
  'fishing-rods': 'angler',
  saws: 'carpenter',
  brushes: 'artist',
  flasks: 'alchemist'
}
const sourceIcon = {
  shop: '/img/icons/others/shops.png',
  location: '/img/icons/others/map.png',
  seal: '/img/items/materials/Dark_Fire.png',
  chest: '/img/items/objects/Treasure_Chest.png',
  personal: '/img/icons/others/personal-request.png',
  bulletin: '/img/icons/others/bulletin-board.png',
  buddy: '/img/icons/others/characters.png',
  slate: '/img/items/valuables/mysterious-slate.png',
  time: '/img/icons/others/legendary-series.png',
  don: '/img/icons/others/merchant.png',
  grove: '/img/icons/others/sapling.png',
  life: '/img/icons/others/life-license.png',
  leafe: '/img/icons/others/leafe.png',
  panther: '/img/items/monsters/Forest_Panther.png',
  ginormosia: '/img/icons/others/teleportation-gate.png'
}

const IMAGE_404 = '/img/image-not-found.png';

function getLifeClassIcon(item) {
  const menu = item.menu_tiers[1].toLowerCase();
  return getIconPath(`/img/icons/stats/stat-life-${iconMap[menu]}.png`)
}
</script>

<template>
  <div class="card-body" data-category="item">
    
    <IconElement :element="item.element" />
    <IconRarity :rarity="item.rarity" />
    <IconElement :element="item.element" />
    <IconRank :rank="item.rank" />

    <ItemRouterLink :item="item" />

    <!-- <ShopList :itemId="item.id" /> -->
    <!-- <LocationList :itemId="item.id" /> -->
    <!-- <StatsTable :item="item" /> -->

  <div class="flex items-center"  v-if="item.stats && (item.menu_tiers[0] === 'Tools' ||  item.menu_tiers[0] === 'Weapons')">
    <div v-if="item.menu_tiers[0] === 'Weapons'">
      <img v-if="item.menu_tiers[1] !== 'Staves'" src="/img/icons/stats/stat-attack.png" class="icon-sm" alt="">
      <img v-if="item.menu_tiers[1] === 'Staves'" src="/img/icons/stats/stat-magic.png" class="icon-sm" alt="">
    </div>
    <div v-if="item.menu_tiers[0] === 'Tools'">
      <img :src="getLifeClassIcon(item)" class="icon-sm" alt="">
    </div>
      {{ item.stats.min?.[0] }} ~ {{ item.stats.max?.[item.stats.max.length - 1] }}
  </div>

  <div v-if="item.menu_tiers[0] === 'Shields'">
    <img src="/img/icons/stats/stat-defense.png" class="icon-sm" alt="">
    {{ item.stats.min?.[0] }} ~ {{ item.stats.max?.[item.stats.max.length - 1] }}
  </div>

  <div v-if="item.menu_tiers[0] === 'Armors'">
    <img src="/img/icons/stats/stat-defense.png" class="icon-sm" alt="">
    {{ item.stats.physical[0] }} ~ {{ item.stats.physical[item.stats.physical.length - 1] }}

    <img src="/img/icons/stats/stat-magic-defense.png" class="icon-sm" alt="">
    {{ item.stats.magic[0] }} ~ {{ item.stats.magic[item.stats.magic.length - 1] }}
  </div>

  <div class="border-2 p-1 my-2 bg-blue-200" v-if="item.reward">
    
    <img 
    :src="getIconPath(sourceIcon[item.reward.types[0]] || IMAGE_404)" 
    class="icon-sm" 
    alt
    />
    {{ item.reward }}
  </div>

  <RecipeTable v-if="item.crafter" :item="item" @open-material-modal="material => emit('open-material-modal', material)" />

  </div>
</template>
