import { createI18n } from 'vue-i18n'
import { DATA } from '@/utils/constants.js'

const SUPPORTED_LANGS = ['en', 'ja']
const DEFAULT_LANG = 'en'
const STORAGE_KEY = 'SET_LANGUAGE'

function buildMessages(data, lang) {
  return data.reduce((acc, entry) => {
    acc[entry.id] = entry[lang] || entry.en || entry.id
    return acc
  }, {})
}

// build messages for all languages
const messages = {}
for (const lang of SUPPORTED_LANGS) {
  messages[lang] = buildMessages(DATA.TRANSLATIONS, lang)
}

// load saved lang
const saved = localStorage.getItem(STORAGE_KEY)
const locale = SUPPORTED_LANGS.includes(saved) ? saved : DEFAULT_LANG

const i18n = createI18n({
  legacy: false,
  locale,
  fallbackLocale: 'en',
  messages,
})

export default i18n
