import { DATA } from '@/utils/constants.js';

export function withBase(input) {
  const basePath = import.meta.env.BASE_URL;
  return  getCleanPath(basePath + '/' + input);
}

export function getCleanPath(input) {
  return input.replace(/\/+/g, '/');
}

export function getBasePath(route) {
  const segments = route.path.split('/');
  return segments[1] || '';
}

const IMAGE_404 = '/img/image-not-found.png';

export function getIconPath(input) {
  const path = resolveIconInput(input);
  const result = withBase(path);
  return input.toLowerCase().includes('icon') ? result.toLowerCase() : result;
}

function resolveIconInput(input) {
  if (input.includes('icons.')) {
    return findIconSrc(input);
  }
  return input;
}

export function findIconSrc(input) {
  const found = DATA.ICONS.find(icon => icon.id === input);
  return found ? found.src : IMAGE_404;
}

// export function itemRouteIdByItemId(itemId) {
//   return itemId.split('.').pop().replace(/_/g, '-');
// }

export function itemIdToRoute(itemId) {
  const route = itemId.replace(/\./g, '/').replace(/_/g, '-');
  return getCleanPath('/' + route);
}

export function getRoutePath(route) {
  return getCleanPath(route);
}

export function getImagePath(itemId) {
  const foundItem = DATA.TRANSLATIONS.find((item) => item.id === itemId);
  if (!foundItem) return IMAGE_404;

  let fileName;
  try {
    fileName = convertLabelToFileName(foundItem.en);
  } catch {
    return withBase(IMAGE_404);
  }

  const [folder, category] = itemId.split('.');
  return withBase(`img/${folder}/${category}/${fileName}.png`);
}

export function convertLabelToFileName(itemLabel) {
  const fineName = itemLabel
    .replace(/'/g, '__apos__')
    .replace(/ /g, '_')
    .replace(/\+/g, '__plus__');
  return stripAccents(fineName);
}

function stripAccents(string) {
  return string.normalize('NFD').replace(/[\u0300-\u036f]/g, '');
}
