export const MenuBuilder = (() => {
  function toNewId(id, replaceWith) {
    return id.replace(/^list\./, `${replaceWith}.`);
  }

  function toNewRoute(route, replaceWith) {
    return route.replace(/^\/list\//, `/${replaceWith}/`);
  }

  function formatWithIdAndRoute(item, replaceWith) {
    return {
      ...item,
      id: toNewId(item.id, replaceWith),
      route: toNewRoute(item.route, replaceWith),
      child: item.child?.map((child) =>
        formatWithIdAndRoute(child, replaceWith),
      ),
    };
  }

  function filterConsumables(item, replaceWith) {
    if (!item.child?.length) return [];
    return item.child
      .filter((child) => {
        const type = child.id.split('.').pop().toLowerCase();
        return type === 'meals' || type === 'potions';
      })
      .map((child) => formatWithIdAndRoute(child, replaceWith));
  }

  function filterObjects(item, replaceWith) {
    if (!item.child?.length) return [];
    return item.child
      .filter((child) => child.recipe_availability === true)
      .map((child) => formatWithIdAndRoute(child, replaceWith));
  }

  function build(
    menuItems,
    allowedCategories,
    replaceWith = 'menu',
    specialConsumables = false,
  ) {
    return menuItems.flatMap((item) => {

      const category = item.id.split('.').pop();
      // console.log(item.id)
      if (!allowedCategories.includes(category)) return [];

      if (specialConsumables && category === 'consumables') {
        return filterConsumables(item, replaceWith);
      }

      if (category === 'objects' || category === 'materials') {
        const newItem = { ...item };
        newItem.child = filterObjects(item, replaceWith);
        return [formatWithIdAndRoute(newItem, replaceWith)];
      }

      const newItem = { ...item };
      newItem.child = item.child?.map((child) =>
        formatWithIdAndRoute(child, replaceWith),
      );
      return [formatWithIdAndRoute(newItem, replaceWith)];
    });
  }

  return { build };
})();
