import { computed } from 'vue'

export function applyCategory(items, selectedCategory) {
  if (!selectedCategory) return items
  return items.filter(item => item.category?.toLowerCase() === selectedCategory.toLowerCase())
}

export const FILTER_OPTIONS = [
   {
    id: 'filter.recipes',
    callback: item => item.recipes && item.recipes.length > 0
  },
  {
    id: 'filter.elements',
     callback: item => item.element && item.element !== ''
  },
]

export function applyFilters(items, selectedFilters = [], mode = 'AND') {
  if (!selectedFilters.length) return items

  return items.filter(item => {
    const results = selectedFilters.map(selectedFilterId => {
      const filterOption = FILTER_OPTIONS.find(option => option.id === selectedFilterId)
      return filterOption?.callback(item) ?? true
    })

    if (mode === 'AND') return results.every(Boolean)
    if (mode === 'OR') return results.some(Boolean)
    if (mode === 'NOT') return results.every(result => !result)
    return true
  })
}
import { DATA } from '@/utils/constants.js'

export function applySort(items, order = 'asc') {
  const lang = localStorage.getItem('SET_LANGUAGE') || 'en'

  const getText = item => {
    const found = DATA.TRANSLATIONS.find(x => x.id === item.id)
    if (!found) return (item.id || '').replace(/'/g, '')
    return (found[lang] || '').replace(/'/g, '')
  }

  return [...items].sort((a, b) => {
    const valA = getText(a).toString()
    const valB = getText(b).toString()
    return order === 'asc'
      ? valA.localeCompare(valB, undefined, { numeric: true, sensitivity: 'base' })
      : valB.localeCompare(valA, undefined, { numeric: true, sensitivity: 'base' })
  })
}
