<script setup>
import { DATA } from '@/utils/constants.js';
import { useI18n } from 'vue-i18n';
const { t } = useI18n();
import { getImagePath } from '@/utils/paths.js';

function getMonstersByMaterial(itemId) {
  return DATA.MONSTERS.data
    .filter(monster => monster.drops?.some(drop => drop.id === itemId))
}

const props = defineProps({
  itemId: {
    type: String,
    required: true
  }
})

</script>

<template>
  <ul class="monster-list">
    <li v-for="monster in getMonstersByMaterial(itemId)" :key="monster.id">
      <img :src="getImagePath(monster.id)" class="icon-sm" alt>
      {{ t(monster.id) }}
    </li>
  </ul>
</template>
