import { defineStore } from 'pinia'
import { ref } from 'vue'

export const useSortStore = defineStore('sort', () => {
  const selectedKey = ref('name') // default sort key
  const direction = ref('asc')    // default direction

  function setSort(key, dir = 'asc') {
    selectedKey.value = key
    direction.value = dir
  }

  function toggleDirection() {
    direction.value = direction.value === 'asc' ? 'desc' : 'asc'
  }

  function resetSort() {
    selectedKey.value = ''
    direction.value = 'asc'
  }

  return { selectedKey, direction, setSort, toggleDirection, resetSort }
})
