<script setup>
import { computed, watch } from "vue"
import { usePaginationStore } from "@/store/paginationStore.js"
import { useRoute, useRouter } from "vue-router"

const route = useRoute()
const router = useRouter()
const paginationStore = usePaginationStore()

// --- Hydrate store from URL on initial load ---
paginationStore.setPage(parseInt(route.query.page) || 1)
paginationStore.setPageSize(parseInt(route.query.size) || 100)

// --- Go to page handler ---
function goToPage(page) {
  paginationStore.setPage(page)
}

// --- Change page size handler ---
function changePageSize(event) {
  paginationStore.setPageSize(parseInt(event.target.value))
}

// --- Compute page numbers ---
const pageNumbers = computed(() => {
  const pages = []
  for (let i = 1; i <= paginationStore.totalPages; i++) pages.push(i)
  return pages
})

// --- Watch store changes → update URL without adding history entries ---
const observePageChange = {
}

watch(
  () => [paginationStore.currentPage, paginationStore.pageSize],
  ([page, size]) => {
    router.replace({
      query: { ...route.query, page, size }
    })
  }
)

watch(
  () => paginationStore.pageSize,
  () => paginationStore.setPage(1)
)

// --- Scroll to top when page changes ---
watch(
  () => paginationStore.currentPage,
  () => window.scrollTo({ top: 0, behavior: "smooth" })
)
</script>

<template>
  <div class="pager flex items-center gap-1">
    <nav class="pagination" aria-label="Pagination">
      <button
        class="page-button page-prev"
        :disabled="paginationStore.currentPage === 1"
        @click="goToPage(paginationStore.currentPage - 1)"
      >
        <span class="material-symbols-outlined">keyboard_arrow_left</span>
      </button>

      <div class="scrollable">
        <button
          v-for="page in pageNumbers"
          :key="page"
          class="page-button page-number"
          :class="{ 'is-active': page === paginationStore.currentPage }"
          @click="goToPage(page)"
        >
          {{ page }}
        </button>
      </div>

      <button
        class="page-button page-next"
        :disabled="paginationStore.currentPage === paginationStore.totalPages"
        @click="goToPage(paginationStore.currentPage + 1)"
      >
        <span class="material-symbols-outlined">keyboard_arrow_right</span>
      </button>
    </nav>

    <select @change="changePageSize" v-model.number="paginationStore.pageSize">
      <option value="10">10</option>
      <option value="20">20</option>
      <option value="50">50</option>
      <option value="100">100</option>
      <option value="300">300</option>
      <option value="500">500</option>
    </select>
  </div>
</template>
