<script setup>
import { ref, computed, watch } from 'vue'
import { trackerStore } from '@/store/trackerStore.js'
import { getIconPath } from '@/utils/paths.js'

const props = defineProps({
  item: { type: Object, required: true },
  showIcon: { type: Boolean, default: false }
})

const { item, showIcon } = props

const isChecked = computed({
  get: () => trackerStore.isChecked(item.id),
  set: () => trackerStore.toggle(item.id)
})
</script>

<template>
  <div class="check-mark">
    <img
      v-if="showIcon"
      :src="getIconPath('/img/icons/others/recipe.png')"
      class="icon-sm"
      alt
    />
    
    <input
      type="checkbox"
      :id="item.id"
      v-model="isChecked"
      class="user-input tracker-input"
    />
  </div>
</template>
