<script setup>
import { useImproviseStore } from '@/store/improviseStore.js'
import { useRoute } from 'vue-router';
import { computed, watch } from 'vue'

const route = useRoute();

const improvLabels = [
  'Improv', 
  '?', 
  '??'
]

const enabledRoutes = [
  '/recipes/consumables/meals',
  '/recipes/consumables/potions',
  '/recipes/objects'
]

const isDisabled = computed(() => !enabledRoutes.includes(route.path))

const improviseStore = useImproviseStore()

watch(
  () => route.path,
  (newPath) => {
    if (!enabledRoutes.includes(newPath)) {
      improviseStore.setOption(0) // reset to 0
    }
  },
  { immediate: true }
)

</script>

<template>
  <select v-model="improviseStore.selectedOption" :disabled="isDisabled" id="improvise-options" name="improvise-options"> 
    <option v-for="(label, index) in improvLabels" :key="index" :value="index">{{ label }}</option>
  </select>
</template>
