<script setup>
import { useI18n } from 'vue-i18n'
import { DATA } from '@/utils/constants.js'
import { getImagePath, getIconPath } from '@/utils/paths.js'
import { RouterLink, useRoute } from 'vue-router'
import BaseModal from '@/components/Shared/BaseModal.vue'
import { ref, computed, onMounted, onBeforeUnmount, inject } from 'vue'
import HowToObtainRecipe from '@/components/Item/HowToObtainRecipe.vue'
import ShopList from '@/components/Item/ShopList.vue'
const { t } = useI18n();
const props = defineProps({ item: Object })
const emit = defineEmits(['open-material-modal'])

function openMaterialModal(material) {
  emit('open-material-modal', material)
}


function getRecipeFramePath(item, rank) {
  const iconMap = {
    1: 'ordinary',
    2: 'fine',
    3: 'notable',
    4: 'supreme',
    5: 'legendary'
  };

  const key = rank ?? item?.rarity ?? 1; // fallback to 1 if nothing
  const finalRank = iconMap[key] || String(key).toLowerCase();

  return `/img/icons/rarity/recipe-frame-${finalRank}.png`;
}

function getExp(level) {
  if (level <= 340) {
    return 0.0722 * level ** 2 - 22.5897 * level + 1824.43;
  } else if (level < 1300) {
    return 46413.3 * Math.pow(1.002026, level - 340);
  } else {
    return 210000;
  }
}

const collapsed = ref([])

function toggleCollapse(index) {
  collapsed.value[index] = collapsed.value[index] === false
}

function getCraftingLevel(item, recipe) {
  const hasLevel = recipe.crafting_level;
  return hasLevel ? recipe.crafting_level : item.rarity;
}
function getLifeClassIcon(item) {
  const lifeClass = item.recipes[0].source.life_class;
  return getIconPath(`/img/icons/life/equipment-${lifeClass.toLowerCase()}.png`)
}

function getConsoleLog(recipe, item) {

  const aaa = {
    'ordinary': 1,
    'fine': 2,
    'notable': 3,
    'supreme': 4,
    'legendary': 5,
  }
  // console.log(recipe.crafting_stat, aaa[recipe.rank.toLowerCase()])
  // console.log(recipe.crafting_stat, recipe.exp, aaa[recipe.rank.toLowerCase()])
  // console.log(recipe.crafting_stat, recipe.exp, item.label)
}
function getRecipeShopSource(item) {
  const shop = findShopByRecipeId(item.id);
  return item.recipes[0].source ?  item.recipes[0].source : shop;
}


function findShopRecipeInfo(recipeId) {
  const shop = DATA.SHOPS.data.find(shop =>
    shop.list.some(
      listItem =>
        listItem.category === 'recipes' &&
        listItem.stocks?.some(stock => stock.id === recipeId)
    )
  );

  if (!shop) return null;

  const stock = shop.list
    .find(listItem => listItem.category === 'recipes')
    .stocks.find(stock => stock.id === recipeId);

  return {
    shopId: shop.id,
    shopName: t(shop.id),
    location: shop.location,
    price: stock.price ?? null,
    currency: shop.currency,
    restriction: stock.restriction ?? null
  };
}

function getCurrencyIconPath(recipeId) {
  const info = findShopRecipeInfo(recipeId);
  if (!info?.currency) return getIconPath(`/img/icons/others/dosh.png`);;
  const fileName = info.currency.replace('_', '-');
  return getIconPath(`/img/icons/others/${fileName}.png`);
}

function getPrice(recipeId) {
  const info = findShopRecipeInfo(recipeId);
  return info.price;
}

function findShopByRecipeId(recipeId) {
  const shop = DATA.SHOPS.data.find(shop =>
    shop.list.some(item =>
      item.category === 'recipes' &&
      item.stocks?.some(stock => stock.id === recipeId)
    )
  );
  return shop ? t(shop.id) : null;
}

function getChestLocation(recipe) {
  if (recipe.source === 'Chest') {
    return recipe.location
  }
}

function getCrafterType(source) {
  return source.replace('Gift from Buddy ', '').toLowerCase();
}

function findCrafters(source) {
  const crafter = source.replace('Gift from Buddy ', '').toLowerCase();

  const crafterClass = {
    blacksmith: ['items.characters.bray', 'items.characters.magmia', 'items.characters.ian'],
    carpenter: ['items.characters.cormell', 'items.characters.cherry', 'items.characters.kunugi'],
    artist: ['items.characters.pierre', 'items.characters.kikyo', 'items.characters.meime'],
    cook: ['items.characters.chakpo', 'items.characters.allan', 'items.characters.nommie'],
    alchemist: ['items.characters.flamel', 'items.characters.sophie', 'items.characters.demiel'],
    tailor: ['items.characters.shea', 'items.characters.klaus', 'items.characters.taylor']
  }
}



function getaaaaa(recipeItemId) {
  const shop = findShopByRecipeId(recipeItemId);
  // don,
  // marco, goddess , fruit
  return shop.label
}

function getShopIdByRecipeItemId(recipeItemId) {
   const shop = findShopByRecipeId(recipeItemId);
  // don,
  // marco, goddess , fruit
  return shop ? shop.label : null; 

}

function isVendorRecipe(item) {
  return item.recipes[0].source.types[0] === 'shop';
}

function isMerchantDonRecipe(item) {
  return getShopIdByRecipeItemId(item.id) === 'Merchant';
}

function getShopId(recipeItemId) {
  return findShopByRecipeId(recipeItemId)?.id;
}

function logThis(value, type) {
  console.log(value, type)
}

function displayThis() {
return true
}

const sourceIcon = {
  shop: '/img/icons/others/shops.png',
  location: '/img/icons/others/map.png',
  seal: '/img/items/materials/Dark_Fire.png',
  chest: '/img/items/objects/Treasure_Chest.png',
  personal: '/img/icons/others/personal-request.png',
  bulletin: '/img/icons/others/bulletin-board.png',
  buddy: '/img/icons/others/characters.png',
  slate: '/img/items/valuables/mysterious-slate.png',
  time: '/img/icons/others/legendary-series.png',
  don: '/img/icons/others/merchant.png',
  grove: '/img/icons/others/sapling.png',
  life: '/img/icons/others/life-license.png',
  leafe: '/img/icons/others/leafe.png',
  panther: '/img/items/monsters/Forest_Panther.png',
  ginormosia: '/img/icons/others/teleportation-gate.png'
}

function getRecipeSourceIcon(type) {

  return getIconPath(sourceIcon[type]);
}

const IMAGE_404 = '/img/image-not-found.png';
</script>

<template>
  <div v-if="item.size" class="item-size">
    <span class="material-symbols-outlined">grid_on</span>
    {{ item.size }}
  </div>

  <ul class="border-y my-2" v-if="displayThis()">
    <li v-for="(recipe, index) in item.recipes" :key="index" class="item-recipe-source">
      <!-- {{ recipe.source }} -->
      <img :src="getIconPath('/img/icons/others/recipe.png')" class="icon-sm" alt>

      <div v-for="(type, index) in recipe.source.types" :key="index" >
        <img 
          :src="getIconPath(sourceIcon[type] || IMAGE_404)" 
          class="icon-sm" 
          alt
        />
      </div>


      <div v-if="isVendorRecipe(item)">

        <img v-if="isMerchantDonRecipe(item)" :src="getIconPath(sourceIcon.don)" class="icon-sm ml-1" alt>
        
        {{ getShopId(item.id) }}
      </div>
      <div v-else>
        {{ recipe.source.requirement }}
      </div>




      <img v-if="recipe.source.life_class" :src="getLifeClassIcon(item)" class="icon-sm ml-1" alt>
      <!-- {{ item.recipes[0].source.life_class }} -->
      <!-- {{ getRecipeShopSource(item) }} -->

    </li>
  </ul>

  <ul class="recipe-list">
    <li v-for="(recipe, index) in item.recipes" :key="index" :data-is-collapsed="collapsed[index] !== false" class="panel recipe-panel">

      <a href="#" class="collapsible-header" @click.prevent="toggleCollapse(index)">
        <div class="flex items-center gap-1">
          <div class="recipe-frame flex items-center">
            <img :src="getIconPath(`/img/icons/life/equipment-${item.crafter.toLowerCase()}.png`)" class="icon-sm" alt />
            <div class="crafting-level relative">
              <div class="absolute left-[5px] top-[3px]">
                <span class="text-[9px]">LV</span>
                <span class="[text-shadow:_0_0_2px_white]" >{{ getCraftingLevel(item, recipe) }}</span>
              </div>
              <img :src="getRecipeFramePath(item, recipe.rank)" class="icon-sm" alt />
            </div>
          </div>
          <img v-if="recipe.rank" :src="getIconPath(`/img/icons/rarity/rank-${recipe.rank}.png`)" class="icon-sm" alt />
          {{ recipe.rank || 'Recipe' }}
          
        </div>

        <span class="material-symbols-outlined icon-expand">add_circle</span>
        <span class="material-symbols-outlined icon-collapse">do_not_disturb_on</span>
      </a>
      <div class="collapsible-content">
        
        <table class="data-table">
          <thead>
            <tr>
              <th class="col-label" scope="col">
                Materials
              </th>
              <th class="col-quantity" scope="col">
                Required
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(material, index) in recipe.materials" :key="index">
              <td>
                <a @click.prevent="openMaterialModal(material)" href="#">
                  <img :src="getImagePath(material.id)" class="icon-sm" alt>
                  {{ t(material.id) }}
                </a>
              </td>
              <td class="col-quantity"><strong>{{ material.quantity }}</strong></td>
            </tr>
          </tbody>
        </table>
        <div class="recipe-crafting-stat">
          <span class="text">Recommended</span>
          <img :src="getIconPath(`/img/icons/stats/stat-life-${item.crafter}.png`)" class="icon-sm" :alt="item.crafter">
          <span class="stat-value">
            {{ recipe.crafting_stat }}
          </span>
        </div>
        <div class="experience-gain">
          <img :src="getIconPath(`/img/icons/stats/stat-exp.png`)" alt="Exp" class="icon-sm" />
          {{ recipe.exp?.toLocaleString() }}
          <!-- {{ getExp(recipe.crafting_stat) }} -->
        </div>
      </div>
    </li>
  </ul>

</template>
