<script setup>
import { RouterLink, useRoute } from 'vue-router';
import { useI18n } from 'vue-i18n';
import { watch, computed } from 'vue';
import { DATA } from '@/utils/constants.js';
import { getRoutePath, getIconPath } from '@/utils/paths.js';
import { MenuBuilder } from '@/utils/MenuBuilder.js';
import MenuLink from '@/components/MenuLink.vue'

const { t } = useI18n();
const route = useRoute();

const menuItems = DATA.MENU_ITEMS;

function isActive(routePath) {
  // return getRoutePath(route.path) === routePath
  return getRoutePath(route.path).startsWith(routePath)
}

const recipeCategories = [
  'weapons',
  'shields',
  'tools',
  'armors',
  'consumables',
  'materials',
  'objects',
]

const allowedSubCategories = [
  'materials/smithing-materials',
  'materials/tailoring-materials',
  'materials/carpentry-materials',
  'materials/artistry-materials',
  'objects/furniture',
  'objects/bed',
  'objects/chair',
  'objects/desk',
  'objects/shelf',
  'objects/mat',
  'objects/workbench',
  'objects/decor',
  'objects/plant',
  'objects/meal',
  'objects/outdoor-furniture',
  'objects/fence',
  'objects/street-lamp',
  'objects/wall-decor',
  'objects/fish-print',
  'objects/wall',
  'objects/floor'
]

const REFINED_MENU_ITEMS = [
  {
    id: "recipes",
    route: "/recipes",
    label_id: "labels.recipes",
    icon: "icons.others.recipe",
    child: MenuBuilder.build(menuItems, recipeCategories, 'recipes', true)
  },
  ...menuItems
];

const activeMenu = computed(() => {
  const cleanPath = getRoutePath(route.path);
  return REFINED_MENU_ITEMS.find(menu => cleanPath.startsWith(menu.route));
});

function getActiveChildStatus(menu, currentPath) {
  const menuIsNotActive = !currentPath.startsWith(menu.route);
  if (menuIsNotActive) return 0;

  const menuHasNoChildren = !menu.child?.length;
  if (menuHasNoChildren) return 0;

  const activeChild = menu.child.find(child => currentPath.startsWith(child.route));
  const noChildIsActive = !activeChild;
  if (noChildIsActive) return 1;

  const activeChildIsPregnant = !!(activeChild.child?.length);
  if (activeChildIsPregnant) return 2;

  return 1;
}

</script>

<template>
  <nav id="app-nav">
    <ul id="main-menus">
      <li :data-id="menu.id"
        v-for="menu in REFINED_MENU_ITEMS" :key="menu.id"
        :data-active-child-level="getActiveChildStatus(menu, getRoutePath(route.path))"
        :class="['menu-item', { 'is-active': activeMenu?.id === menu.id }]">

        <MenuLink :menu-item="menu" iconSize="md" />

        <ul v-if="menu.child?.length" class="menu-tier-list menu-tier-1">
          <li :data-id="menuTier1.id"
            v-for="menuTier1 in menu.child" :key="menuTier1.id"
            :class="['menu-tier-1-item', { 'is-active': isActive(menuTier1.route) }]">

            <MenuLink :menu-item="menuTier1" />

            <ul v-if="menuTier1.child?.length" class="menu-tier-list menu-tier-2">
              <li :data-id="menuTier2.id"
                v-for="menuTier2 in menuTier1.child" :key="menuTier2.id"
                :class="['menu-tier-2-item', { 'is-active': isActive(menuTier2.route) }]">

                <MenuLink :menu-item="menuTier2" />
                
              </li>
            </ul>
          </li>
        </ul>
      </li>
    </ul>
  </nav>
</template>
