function getAllRoutes(items) {
  const routes = []

  items.forEach(item => {
    if (item.route) {
      routes.push(item.route)
    }
    if (item.child && item.child.length) {
      routes.push(...getAllRoutes(item.child))
    }
  })

  return routes
}

// Example usage:
const menuData = DATA.MENU_ITEMS
