<script setup>
import { ITEM_DATA } from '@/utils/constants.js';
import TEMP_DATA from '@/utils/temp-data.json';

const arrayColumns = [
  'menu_tiers', 
  'stats__min', 'stats__max', 'stats__physical', 'stats__magic',
  'stone_exchange', 'price__buy__sell',
  'recipes__0__source__types', 'reward__types', 
];

const booleanColumns = ['tracker', 'is_housing_item'];
const arraySeparator = ' | ';

function flatten(obj, parentKey = '', res = {}) {
  for (let [key, value] of Object.entries(obj)) {
    const newKey = parentKey ? `${parentKey}__${key}` : key;

    if (Array.isArray(value)) {
      if (value.every(v => v !== null && typeof v !== 'object')) {
        // array of primitives → join as string
        res[newKey] = value.join(',');
      } else {
        // array of objects → index
        value.forEach((v, i) => flatten(v, `${newKey}__${i}`, res));
      }
    } else if (value !== null && typeof value === 'object') {
      flatten(value, newKey, res);
    } else {
      res[newKey] = value;
    }
  }
  return res;
}

function unflatten(row) {
  const result = {};

  for (let [flatKey, value] of Object.entries(row)) {
    if (value === '' || value === null || value === undefined) continue;

    const keys = flatKey.split('__');
    let cur = result;

    keys.forEach((k, i) => {
      const isLast = i === keys.length - 1;
      const nextIsIndex = !isLast && /^\d+$/.test(keys[i + 1]);

      if (isLast) {
        if (booleanColumns.includes(flatKey)) {
          cur[k] = String(value).toUpperCase() === 'TRUE';
        } else if (arrayColumns.includes(flatKey)) {
          cur[k] = String(value).split(arraySeparator).map(v => v.trim());
        } else {
          cur[k] = value;
        }
      } else {
        if (!(k in cur)) cur[k] = nextIsIndex ? [] : {};
        cur = cur[k];
      }
    });
  }

  return result;
}


const KEYS = [
  // 'ARMORS',        // 0
  // 'CHARACTERS',    // 1
  // 'CONSUMABLES',   // 2
  'GATHERABLES',   // 3
  'LOCATIONS',     // 4
  // 'MATERIALS',     // 5
  // 'MONSTERS',      // 6
  // 'MOUNTS',        // 7
  // 'OBJECTS',       // 8
  'SHOPS',         // 10
  // 'STRANGELINGS',  // 11
  // 'SHIELDS',       // 9
  // 'TOOLS',         // 12
  // 'WEAPONS',       // 13
  'GIFTS'          // 14
];

const jsonData = ITEM_DATA.OBJECTS;

// flatten for CSV
const flat = jsonData.data.map(obj => flatten(obj));
// console.log(flat);

// unflatten back to JSON
const csvData = TEMP_DATA;
const rebuilt = csvData.map(row => unflatten(row));
// console.log(JSON.stringify(rebuilt, null, 2));
console.log(rebuilt);

</script>

