<script setup>
import { useSortStore } from '@/store/sortStore.js'

const sortStore = useSortStore()

const sortOptions = [
  { key: 'name', label: 'Name' },
  { key: 'rarity', label: 'Rarity' },
]
</script>

<template>
  <div class="flex items-center gap-1">
    <select v-model="sortStore.selectedKey">
      <option value="">Sort</option>
      <option v-for="o in sortOptions" :key="o.key" :value="o.key">{{ o.label }}</option>
    </select>

    <button @click="sortStore.toggleDirection">
      <span class="material-symbols-outlined">
        {{ sortStore.direction === 'asc' ? 'arrow_upward' : 'arrow_downward' }}
      </span>
    </button>

    <button @click="sortStore.resetSort">
      <span class="material-symbols-outlined">restart_alt</span>
    </button>
  </div>
</template>
