<script setup>
import { ref, watch, onMounted, onBeforeUnmount } from 'vue'
import { useI18n } from 'vue-i18n'
import { DATA, ITEM_DATA } from '@/utils/constants.js'
import { getImagePath, itemIdToRoute } from '@/utils/paths.js'

const { t } = useI18n()
const query = ref('')
const suggestions = ref([])
const containerRef = ref(null)
const allItems = Object.values(ITEM_DATA).flatMap(obj => obj.data)

const normalize = (str) =>
  str.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, '')

watch(query, (queryValue) => {
  if (!queryValue) {
    suggestions.value = []
    return
  }

  const search = normalize(queryValue.trim())
  suggestions.value = allItems
    .map(item => {
      const name = normalize(t(item.id))
      const id = normalize(item.id)
      const nameIndex = name.indexOf(search)
      const idIndex = id.indexOf(search)
      const matchIndex = nameIndex >= 0 ? nameIndex : idIndex >= 0 ? idIndex : -1
      return matchIndex >= 0 ? { item, matchIndex } : null
    })
    .filter(Boolean)
    .sort((a, b) => a.matchIndex - b.matchIndex)
    .map(entry => entry.item)
    .slice(0, 10)
})

// Close suggestions when clicking outside
function handleClickOutside(event) {
  if (containerRef.value && !containerRef.value.contains(event.target)) {
    suggestions.value = []
  }
}

// Close suggestions on ESC key
function handleKeydown(event) {
  if (event.key === 'Escape') {
    suggestions.value = []
  }
}

onMounted(() => {
  document.addEventListener('click', handleClickOutside)
  document.addEventListener('keydown', handleKeydown)
})
onBeforeUnmount(() => {
  document.removeEventListener('click', handleClickOutside)
  document.removeEventListener('keydown', handleKeydown)
})

const onFocus = (event) => {
  if (query.value) {
    event.target.select()
  }
}

function onSelectItem() {
  query.value = ''
  suggestions.value = []
}

</script>
<template>
  <div ref="containerRef" class="relative flex-1 mx-2">
    <div class="absolute top-[4px] left-[5px]">
      <span class="material-symbols-outlined">search</span>
    </div>

    <input
      type="text"
      v-model="query"
      placeholder="Search items..."
      class="user-input search-input"
      @focus="onFocus"
    />

    <ul
      v-if="suggestions.length"
      class="absolute z-10 w-full bg-white border rounded mt-1 max-h-120 overflow-auto shadow-lg"
    >
      <li
        v-for="item in suggestions"
        :key="item.id"
        class="px-3 py-2 cursor-pointer hover:bg-blue-100"
      >
        <RouterLink
          :to="itemIdToRoute(item.id)"
          class="flex items-center gap-2"
          @click="onSelectItem"
        >
          <img :src="getImagePath(item.id)" class="icon-sm" alt />
          {{ t(item.id) }}
        </RouterLink>
      </li>
    </ul>
  </div>
</template>
