<script setup>
import { watch, computed } from 'vue'
import { useGridStore } from '@/store/gridStore.js'
import { getBasePath } from '@/utils/paths.js';
import { useRoute } from 'vue-router'
const route = useRoute()

const basePath = computed(() => getBasePath(route))
const menuTier1 = computed(() => route.params.menuTier1)

const gridStore = useGridStore()

gridStore.setGridNumber(basePath.value, menuTier1.value)

watch([basePath, menuTier1], ([newBasePath, newMenuTier1]) => {
  gridStore.setGridNumber(newBasePath, newMenuTier1)
})
</script>

<template>
  <select v-model="gridStore.selectedGridNumber" id="grid-options" name="grid-options">
    <option v-for="number in 6" :key="number" :value="number">Grid: {{ number }}</option>
  </select>
</template>
