<script setup>
import { DATA } from '@/utils/constants.js';
import { useI18n } from 'vue-i18n';
const { t } = useI18n();
import { getIconPath } from '@/utils/paths.js';

const props = defineProps({
  item: {
    type: Object,
    required: true
  }
})

function getRecipeSource(item) {
  const shopId = findShopByRecipeId(item.id);
  return item.recipe_source ? item.recipe_source : shopId;
}

const SHOPS = DATA.SHOPS.data;

function findShopByRecipeId(recipeId) {
  const shop = SHOPS.find(shop =>
    shop.list.some(item =>
      item.category === 'recipes' &&
      item.stocks?.some(stock => stock.id === recipeId)
    )
  );
  return shop ? t(shop.id) : null;
}

</script>

<template>
