<script setup>
import { DATA } from '@/utils/constants.js'
import BaseModal from '@/components/Shared/BaseModal.vue'
import MaterialModal from '@/components/Shared/MaterialModal.vue'
import { ref, computed, onMounted, onBeforeUnmount, inject, onActivated } from 'vue'
import { applyCategory, applyFilters, applySort, FILTER_OPTIONS } from '@/composables/useFilters.js'
import { useI18n } from 'vue-i18n'
import { getImagePath, getRoutePath, itemIdToRoute, getIconPath } from '@/utils/paths.js'
import ShopList from '@/components/Item/ShopList.vue'
import RecipeTable from '@/components/Item/RecipeTable.vue'
import ItemSkills from '@/components/Item/ItemSkills.vue'
import AgedSkills from '@/components/Item/AgedSkills.vue'
import InOtherLanguages from '@/components/Item/InOtherLanguages.vue'
import StatsTable from '@/components/Item/StatsTable.vue'
import IconElement from '@/components/Item/IconElement.vue'
import IconRarity from '@/components/Item/IconRarity.vue'
import IconRank from '@/components/Item/IconRank.vue'
import IconTimeOfDay from '@/components/Item/IconTimeOfDay.vue'
import { RouterLink, useRoute } from 'vue-router';
import ShoppingTable from '@/components/Item/ShoppingTable.vue'
import LifeType from '@/components/Item/LifeType.vue'
const { t } = useI18n()
const route = useRoute()

console.log(route)

const KEY = route.params.menuTier1 === 'code' ? 'GIFTS' : route.params.menuTier1.toUpperCase();

function routeToItemId() {
  const path = route.path;
  const cleanPath = path.replace(/^\/+/, '')  // remove leading slashes
  return cleanPath.replace(/\//g, '.').replace(/-/g, '_')
}

const itemId = routeToItemId();



const item = DATA[KEY].data.find(item => item.id === itemId) || [];

// Modal
const showModal = ref(false)
const selectedMaterial = ref(null)
function openMaterialModal(material) {
  selectedMaterial.value = material
  showModal.value = true
}


</script>

<template>
  <section class="rounded-[20px] p-3 bg-white item-profile">

    <h1>{{ t(item.id) }}</h1>
    <img :src="getImagePath(item.id)" class="max-h-[120px]" alt />
    
    <IconElement :element="item.element" />
    <IconRarity :rarity="item.rarity" />
    <IconElement :element="item.element" />
    <IconRank :rank="item.rank" />

    <RecipeTable :item="item" @open-material-modal="openMaterialModal" />
  </section>

  <BaseModal v-model="showModal">
    <MaterialModal v-if="selectedMaterial" :itemId="selectedMaterial.id" />
  </BaseModal>
</template>
