import { computed } from 'vue';
import { DATA } from '@/utils/constants.js';
import { applyFilters } from './useFilters.js';
import { getBasePath } from '@/utils/paths.js';
import { useRoute } from 'vue-router';

export function useFilteredItems({ props, filters }) {
  const route = useRoute();
  const basePath = computed(() => getBasePath(route));

  function filterByTier(items, tierIndex, tierValue) {
    if (!tierValue) return items;
    return items.filter(
      (item) =>
        item.menu_tiers?.[tierIndex]?.toLowerCase() === tierValue.toLowerCase()
    );
  }

  function createRecipeItems(data) {
    return data.filter(item => item.crafter);
  }

  function isRecipeList() {
    return basePath.value === 'recipes';
  }

  const allItems = computed(() => {
    const KEY = props.menuTier1.toUpperCase();
    const data = DATA[KEY]?.data || [];
    return isRecipeList() ? createRecipeItems(data) : data;
  });

  const tier1Items = computed(() => filterByTier(allItems.value, 0, props.menuTier1));
  const tier2Items = computed(() => filterByTier(tier1Items.value, 1, props.menuTier2));
  const tier3Items = computed(() => filterByTier(tier2Items.value, 2, props.menuTier3));
  const refinedItems = computed(() =>
    applyFilters(tier3Items.value, filters.selected, filters.mode)
  );

  const counter = computed(() => ({
    menuTier1: tier1Items.value.length,
    menuTier2: tier2Items.value.length,
    menuTier3: tier3Items.value.length,
  }));

  return { refinedItems, counter };
}
