<script setup>
import { ref, computed, onMounted, watch, reactive } from 'vue'
import BaseModal from '@/components/Shared/BaseModal.vue'
import MaterialModal from '@/components/Shared/MaterialModal.vue'
import ShopCard from '@/components/Card/ShopCard.vue'
import MonsterCard from '@/components/Card/MonsterCard.vue'
import LocationCard from '@/components/Card/LocationCard.vue'
import GiftCard from '@/components/Card/GiftCard.vue'
import ItemCard from '@/components/Card/ItemCard.vue'
import CharacterCard from '@/components/Card/CharacterCard.vue'
import SourceCard from '@/components/Card/SourceCard.vue'
import RecipeCard from '@/components/Card/RecipeCard.vue'
import { useFilteredItems } from '@/composables/useFilteredItems.js'
import { useGridStore } from '@/store/gridStore.js'
import { usePaginationStore } from '@/store/paginationStore.js'
import { DATA } from '@/utils/constants'
import TrackerCheckbox from '@/components/Shared/TrackerCheckbox.vue'
import { trackerStore } from '@/store/trackerStore.js'
import { getBasePath, getIconPath } from '@/utils/paths.js';
import { useRoute } from 'vue-router';
import { useImproviseStore } from '@/store/improviseStore.js'
import { useSortStore } from '@/store/sortStore.js'
import { sortBy } from '@/utils/sorters.js'

import TYPE___ from '@/assets/data/others/mat-type.json';

// C:\Projects\my-fantasy-life\src\assets\data\others\mat-type.json

const dataA = DATA.MATERIALS.data;

function parseMaterials(value) {
  const found =  TYPE___.find(item => item.label === value)
  return found ? found.material_type : "";
}
function addMaterialTypeToMenuTiers(data) {
  return data.map(item => {
    const material_type = parseMaterials(item.materials)?.type || item.material_type
    const menu_tiers = Array.isArray(item.menu_tiers) ? item.menu_tiers : []

    return {
      ...item,
      material_type,
      menu_tiers: menu_tiers.includes(material_type)
        ? menu_tiers
        : [...menu_tiers, material_type]
    }
  })
}

const test = addMaterialTypeToMenuTiers(dataA)
// console.log('TYPE___', test)
const props = defineProps({
  menuTier1: { type: String, default: '' },
  menuTier2: { type: String, default: '' },
  menuTier3: { type: String, default: '' },
})

const { menuTier1, menuTier2, menuTier3 } = props;

const route = useRoute();
const basePath = computed(() => getBasePath(route));

// translationMap
const lang = localStorage.getItem('SET_LANGUAGE') || 'en'
const translationMap = new Map(
  DATA.TRANSLATIONS.map(x => {
    const value =
      lang === 'ja'
        ? x['ja-yomi'] || x['ja'] || x.id
        : x[lang] || x.id
    return [x.id, (value || '').replace(/'/g, '')]
  })
)

// sort
const sortStore = useSortStore()
const sortedItems = computed(() => {
  if (!sortStore.selectedKey) return refinedItems.value
  const fn = sortBy[sortStore.selectedKey]
  if (!fn) return refinedItems.value
  return [...refinedItems.value].sort((a, b) =>
    fn(a, b, sortStore.direction, translationMap)
  )
})

// Grid Number
const gridStore = useGridStore()
const gridClass = computed(() => `item-list grid-cols-${gridStore.selectedGridNumber}`)

// Modal
const showModal = ref(false)
const selectedMaterial = ref(null)
function openMaterialModal(material) {
  selectedMaterial.value = material
  showModal.value = true
}

// Card Components
const cardComponent = computed(() => {
  if (basePath.value === 'recipes') return RecipeCard
  const map = {
    gifts: GiftCard,
    monsters: MonsterCard,
    characters: CharacterCard,
    shops: ShopCard,
    locations: LocationCard,
    source: SourceCard
  }
  return map[menuTier1] || ItemCard
})

// Category Filter
const filters = {
  selected: ref([]),
  mode: ref('AND')
}

const { refinedItems, counter } = useFilteredItems({ props, filters })

// Pagination
const paginationStore = usePaginationStore()

watch(
  refinedItems,
  (items) => {
    paginationStore.totalItems = items.length
    if (paginationStore.currentPage > paginationStore.totalPages) {
      paginationStore.setPage(paginationStore.totalPages)
    }
  },
  { immediate: true }
)

const paginatedItems = computed(() => {
  const start = (paginationStore.currentPage - 1) * paginationStore.pageSize
  return sortedItems.value.slice(start, start + paginationStore.pageSize)
})

// isTrackable
function isTrackable(item) {
  const categories = ['monsters', 'gatherables', 'strangelings', 'characters', 'mounts', 'gifts'];
  return categories.includes(menuTier1) || item.tracker
}

// Improvise Recipe
const improviseStore = useImproviseStore()

</script>

<template>
  <section class="content">

    <h1 class="text-[30px] capitalize my-2">
      {{ menuTier1 }} ({{ counter.menuTier1 }})
      <span v-if="menuTier2"> - {{ menuTier2 }} ({{ counter.menuTier2 }})</span>
      <span v-if="menuTier3"> - {{ menuTier3 }} ({{ counter.menuTier3 }})</span>
    </h1>

    <ol :class="gridClass">

      <!-- Improvise Card -->
      <li v-for="improviseCard in improviseStore.selectedOption">
        <img :src="getIconPath(`/img/icons/others/improv-${improviseCard}.png`)" alt />
      </li>

      <!-- Card -->
      <li v-for="(item, index) in paginatedItems" :key="item.id" class="card-item" :data-is-checked="trackerStore.isChecked(item.id)">

        <div class="card">

          <div class="card-header">
            <div class="card-index">
              {{ (paginationStore.currentPage - 1) * paginationStore.pageSize + index + 1 }}
            </div>
            <div v-if="item.version !== '1.0.0'" class="item-version text-[12px] bg-blue-500 text-white px-2 rounded-full">
              {{ item.version }}
            </div>

            <TrackerCheckbox v-if="isTrackable(item)" :item="item" />
          </div>

          <component
            :is="cardComponent"
            :item="item"
            @open-material-modal="openMaterialModal" />

        </div>
      </li>

    </ol>

    <BaseModal v-model="showModal">
      <MaterialModal v-if="selectedMaterial" :itemId="selectedMaterial.id" />
    </BaseModal>
  </section>
</template>