import { defineStore } from 'pinia'
import { ref } from 'vue'

export function shouldUseFourColumns(basePath, menuTier1) {
  return ['recipes', 'recipe-source'].includes(basePath) ||
          ['strangelings', 'characters', 'gifts', 'shops', 'locations'].includes(menuTier1)
}

export const useGridStore = defineStore('gridStore', () => {
  const selectedGridNumber = ref(5)

  function setGridNumber(basePath, menuTier1) {
    selectedGridNumber.value = shouldUseFourColumns(basePath, menuTier1) ? 4 : 4
    // selectedGridNumber.value = 4
  }

  return { selectedGridNumber, setGridNumber }
})