<script setup>
import { useI18n } from 'vue-i18n'
import { RouterLink } from 'vue-router'
import { getImagePath, itemIdToRoute } from '@/utils/paths.js'

const props = defineProps({
  item: { type: Object, required: true },
  showImage: { type: Boolean, default: true }
})

const { t, locale } = useI18n()

function selectItem(item) {
  selectedItemStore.setItemId(item.id)
}

function showLabel() {
  return true
  return locale.value !== 'en'
}
function showId() {
  return false
  // return true
}
</script>

<template>
  <RouterLink :to="itemIdToRoute(item.id)" class="item-route-link">
    <img v-if="showImage" :src="getImagePath(item.id)" class="item-image" alt />
    <h2>{{ t(item.id) }}</h2>
  </RouterLink>
  <h3 v-if="showLabel()" class="item-label">{{ item.label }}</h3>
  <div v-if="showId()">{{ item.id }}</div>
</template>
